import React from 'react'

const FileIcon = ({icon}) => <i className={`icon file-${icon}`} />

const FileList = ({files}) => <div className="file-list">
  <div className="file-list-wrapper">{files.map(({icon, name}) => <div className="file">
    <FileIcon icon={icon} /><span>{name}</span>
  </div>)}</div>
</div>

export default FileList


